<!-->会员注册页面<-->
<template>
  <div id="memberRegister">
    <el-steps class="steps" :active="active" simple>
      <el-step title="会员信息" icon="el-icon-tickets"></el-step>
      <el-step title="代表作品" icon="el-icon-edit-outline"></el-step>
      <el-step title="注册" icon="el-icon-upload"></el-step>
    </el-steps>
    <keep-alive>
      <component :is="this.currentPath" v-on:changeStep="changeStep"></component>
    </keep-alive>
  </div>
</template>

<script>
import First from "@/components/userCenter/register/first";
import Second from "@/components/userCenter/register/second";
import Third from "@/components/userCenter/register/third";
export default {
  name: "memberRegister",
  components: { First, Second, Third },
  data() {
    return {
      currentPath: "first",
      active: 0
    };
  },
  methods: {
    changeStep(i) {
      if (i == 0) {
        this.active = 0;
        this.currentPath = "first";
      }
      if (i == 1) {
        this.active = 1;
        this.currentPath = "second";
      }
      if (i == 2) {
        this.active = 2;
        this.currentPath = "third";
      }
    },
    init() {
      //显示路径
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == "会员中心"
      );
      global.currentPath.data = [index,1];
    }
  },
  activated() {
    this.init();
  },
};
</script>


<style lang="scss" scoped>
#memberRegister {
  height: 100%;
  width: 1200px;
  padding: 50px;
  margin: 10px auto;
  background: rgba(235, 240, 247, 0.932);
  border-radius: 2px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.steps {
  margin-left: 30px;
  margin: 20px 0 50px 30px;
}
</style>



