<!-->会员注册页面<-->
<template>
  <div id="second">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="品牌" prop="brand">
        <el-select v-model="ruleForm.brand" placeholder="选择品牌" multiple>
          <el-option v-for="(item,index) in options.brand" :key="index" :value="item.name">
            <span style="text-align:center; color: #8492a6; font-size: 13px">{{ item.name }}</span>
          </el-option>
        </el-select>
        <el-input style="width:auto;" v-model="brand2" placeholder="其他，可选"></el-input>
      </el-form-item>
      <el-form-item label="版本" prop="version">
        <el-input v-model="ruleForm.version"></el-input>
      </el-form-item>
      <el-form-item label="应用领域" prop="applicationArea">
        <el-select v-model="ruleForm.applicationArea" placeholder="选择应用领域" multiple>
          <el-option v-for="(item,index) in options.applicationArea" :key="index" :value="item.name">
            <span style="text-align:center; color: #8492a6; font-size: 13px">{{ item.name }}</span>
          </el-option>
        </el-select>
        <el-input style="width:auto;" v-model="applicationArea2" placeholder="其他，可选"></el-input>
      </el-form-item>
      <el-form-item label="应用行业" prop="applicationIndustry">
        <el-select v-model="ruleForm.applicationIndustry" placeholder="选择所在行业" multiple>
          <el-option v-for="(item,index) in options.applicationIndustry" :key="index" :value="item.name">
            <span style="text-align:center; color: #8492a6; font-size: 13px">{{ item.name }}</span>
          </el-option>
        </el-select>
        <el-input style="width:auto;" v-model="applicationIndustry2" placeholder="其他，可选"></el-input>
      </el-form-item>
      <el-form-item label="应用场景" prop="applicationScenario">
        <el-input v-model="ruleForm.applicationScenario"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="nextStep('ruleForm')">跳过</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { option } from "@/common/registerOp.js";
import { insertRepresentativeWork } from "@/api/sign.js";

export default {
  name: "second",
  data() {
    return {
      ruleForm: {},
      rules: {},
      options: [],
      formData: new FormData(),
      applicationIndustry2: "",
      brand2: "",
      applicationArea2: ""
    };
  },
  created() {
    this.ruleForm = option.secondForm;
    this.rules = option.secondRules;
    this.options = option.secondOptions;
  },
  methods: {
    nextStep(formName) {
      this.$confirm("确认跳过？", "提示", {
        confirmButtonText: "继续",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$emit("changeStep", 2);
      });
    },
    submitForm(formName) {
      let that = this;
      that
        .$confirm("确认信息无误？", "提示", {
          confirmButtonText: "继续",
          cancelButtonText: "取消",
          type: "warning"
        })
        .then(() => {
          let data = Object.assign({}, that.ruleForm);
          Object.keys(data).forEach(function(key) {
            if (
              //拼接多选与“其他”
              key == "applicationIndustry" ||
              key == "brand" ||
              key == "applicationArea"
            ) {
              if (that.$data[key + "2"]) {
                data[key] = data[key].join(",") + "," + that.$data[key + "2"];
              }
            }
            that.formData.append(key, data[key]);
          });
          that.$refs[formName].validate(valid => {
            if (valid) {
              insertRepresentativeWork(that.formData)
                .then(res => {
                  if (res.result == "success") {
                    that.$refs[formName].resetFields();
                    that.$message({
                      showClose: true,
                      dangerouslyUseHTMLString: true,
                      message:
                        "<p>提交成功,你可以继续添加代表作品或点击跳过进入下一步</p>",
                      type: "success",
                      duration: 5000
                    });
                  } else {
                    that.$message({
                      showClose: true,
                      message: res.message,
                      type: "error",
                      duration: 2000
                    });
                  }
                })
                .catch(err => {
                  that.$message({
                    showClose: true,
                    message: err.message,
                    type: "error",
                    duration: 2000
                  });
                });
            } else {
              that.$message({
                showClose: true,
                dangerouslyUseHTMLString: true,
                message: "<p>提交失败,请检查表单是否有误</p>",
                type: "error",
                duration: 3000
              });
            }
          });
        });
    }
  }
};
</script>


<style lang="scss" scoped>
</style>



