<!-->会员注册页面<-->
<template>
  <div id="first">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="企业名称" prop="enterpriseName">
        <el-input v-model="ruleForm.enterpriseName" placeholder="请输入合法企业中文名"></el-input>
      </el-form-item>
      <el-form-item label="企业类型" prop="enterpriseType">
        <el-input v-model="ruleForm.enterpriseType" placeholder="企业经营/个体经营"></el-input>
      </el-form-item>
      <el-form-item label="企业性质" prop="enterpriseNature">
        <el-select v-model="ruleForm.enterpriseNature" placeholder="选择企业性质" multiple>
          <el-option v-for="(item,index) in options.enterpriseNature" :key="index" :value="item.name">
            <span style="text-align:center; color: #8492a6; font-size: 13px">{{ item.name }}</span>
          </el-option>
        </el-select>
        <el-input style="width: auto;" v-model="enterpriseNature2" placeholder="其他，选填"></el-input>
      </el-form-item>
      <el-form-item label="企业规模" prop="enterpriseScale">
        <el-input v-model="ruleForm.enterpriseScale"></el-input>
      </el-form-item>
      <el-form-item label="所在地" prop="location">
        <el-select v-model="ruleForm.location" placeholder="选择所在城市或地区" multiple>
          <el-option v-for="(item,index) in options.location" :key="index" :value="item.name">
            <span style="text-align:center; color: #8492a6; font-size: 13px">{{ item.name }}</span>
          </el-option>
        </el-select>
        <el-input style="width: auto;" v-model="location2" placeholder="其他，选填"></el-input>
      </el-form-item>
      <el-form-item label="注册资本" prop="registeredCapital">
        <el-input v-model="ruleForm.registeredCapital"></el-input>
      </el-form-item>
      <el-form-item label="注册年份" prop="registeredDate">
        <el-col :span="8">
          <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.registeredDate" style="width: 100%;"></el-date-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="经营模式" prop="managementModel">
        <el-input v-model="ruleForm.managementModel" placeholder="制造商/贸易商"></el-input>
      </el-form-item>
      <el-form-item label="经营范围" prop="managementScope">
        <el-input v-model="ruleForm.managementScope" placeholder="如：工业机器人、技术开发等"></el-input>
      </el-form-item>
      <el-form-item label="主营行业" prop="mainCamp">
        <el-input v-model="ruleForm.mainCamp" placeholder="如：机械本体、控制器等"></el-input>
      </el-form-item>
      <el-form-item label="擅长应用" prop="mainApplication">
        <el-input v-model="ruleForm.mainApplication" placeholder="如：弧焊、激光切割等"></el-input>
      </el-form-item>
      <el-form-item label="发展历程" prop="developing">
        <el-input v-model="ruleForm.developing" placeholder="如：1998年开始成立公司等"></el-input>
      </el-form-item>
      <el-form-item label="合作伙伴" prop="cooperativePartner">
        <el-input v-model="ruleForm.cooperativePartner" placeholder="如：同济大学、浙江大学等"></el-input>
      </el-form-item>
      <el-form-item label="主要客户" prop="mainCustomer">
        <el-input v-model="ruleForm.mainCustomer"></el-input>
      </el-form-item>
      <el-form-item label="邮政编码" prop="postalCode">
        <el-input v-model="ruleForm.postalCode"></el-input>
      </el-form-item>
      <el-form-item label="联系电话" prop="contactNumber">
        <el-input v-model="ruleForm.contactNumber"></el-input>
      </el-form-item>
      <el-form-item label="传真" prop="fax">
        <el-input v-model="ruleForm.fax" placeholder="如：0860-592-88887777"></el-input>
      </el-form-item>
      <el-form-item label="电子邮件" prop="email">
        <el-input v-model="ruleForm.email"></el-input>
      </el-form-item>
      <el-form-item label="联系地址" prop="contactAddress">
        <el-input v-model="ruleForm.contactAddress"></el-input>
      </el-form-item>
      <el-form-item label="联系人" prop="contacts">
        <el-input v-model="ruleForm.contacts"></el-input>
      </el-form-item>
      <el-form-item label="所在部门" prop="department">
        <el-select v-model="ruleForm.department" placeholder="选择所在部门" multiple>
          <el-option v-for="(item,index) in options.department" :key="index" :value="item.name">
            <span style="text-align:center; color: #8492a6; font-size: 13px">{{ item.name }}</span>
          </el-option>
        </el-select>
        <el-input style="width: auto;" v-model="department2" placeholder="其他，选填"></el-input>
      </el-form-item>
      <el-form-item label="职务" prop="post">
        <el-select v-model="ruleForm.post" placeholder="选择职务" multiple>
          <el-option v-for="(item,index) in options.post" :key="index" :value="item.name">
            <span style="text-align:center; color: #8492a6; font-size: 13px">{{ item.name }}</span>
          </el-option>
        </el-select>
        <el-input style="width: auto;" v-model="post2" placeholder="其他，选填"></el-input>
      </el-form-item>
      <el-form-item label="qq" prop="qq">
        <el-input v-model="ruleForm.qq"></el-input>
      </el-form-item>
      <el-form-item label="wechat" prop="wechat">
        <el-input v-model="ruleForm.wechat"></el-input>
      </el-form-item>
      <el-form-item label="资料认证" prop="authenticationDatas">
        <el-upload :http-request="setFile1" class="upload-demo" action='' :limit=1 :multiple=false ref="upload" :auto-upload="true">
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传zip文件，且不超过2000kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item label="联络人资料" prop="contactInfoDatas">
        <el-upload :http-request="setFile2" class="upload-demo" action='' :limit=1 :multiple=false ref="upload" :file-list="fileList_2" :auto-upload="true">
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传zip文件，且不超过2000kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="nextStep('ruleForm')">提交-下一步</el-button>
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { option } from "@/common/registerOp.js";
import { insertNewMember } from "@/api/sign.js";

export default {
  name: "first",
  data() {
    return {
      ruleForm: {},
      rules: {},
      fileList_1: [],
      fileList_2: [],
      enterpriseNature2: "",
      location2: "",
      department2: "",
      post2: "",
      options: [],
      formData: new FormData()
    };
  },
  created() {
    this.ruleForm = option.firstForm;
    this.rules = option.firstRules;
    this.options = option.firstOptions;
  },
  methods: {
    nextStep(formName) {
      let that = this;
      that
        .$confirm("提交则无法返回，确认信息无误？", "提示", {
          confirmButtonText: "继续",
          cancelButtonText: "取消",
          type: "warning"
        })
        .then(() => {
          // let file = {
          //     authenticationDatas:that.fileList_1[0],
          //     contactInfoDatas:that.fileList_2[0]
          //   }
          // let data = Object.assign({},that.ruleForm,file)
          // let formData = new FormData();
          // console.log(that.fileList_1[0])
          let data = JSON.parse(JSON.stringify(that.ruleForm));
          Object.keys(data).forEach(function(key) {
            if (
              key == "enterpriseNature" ||
              key == "location" ||
              key == "department" ||
              key == "post"
            ) {
              if (that.$data[key + "2"]) {
                data[key] = data[key].join(",") + "," + that.$data[key + "2"];
              }
            }
            that.formData.append(key, data[key]);
          });
          that.$refs[formName].validate(valid => {
            if (valid) {
              insertNewMember(that.formData)
                .then(res => {
                  if (res.result == "success") {
                    that.$refs[formName].resetFields();
                    that.$message({
                      showClose: true,
                      message: "提交成功",
                      type: "success",
                      duration: 2000
                    });
                    that.$emit("changeStep", 1);
                  } else {
                    that.$message({
                      showClose: true,
                      message: res.message,
                      type: "error",
                      duration: 2000
                    });
                  }
                  Object.keys(data).forEach(function(key) {
                    that.formData.delete(key);
                  });
                })
                .catch(err => {
                  that.$message({
                    showClose: true,
                    message: err.message,
                    type: "error",
                    duration: 2000
                  });
                  Object.keys(data).forEach(function(key) {
                    that.formData.delete(key);
                  });
                });
            } else {
              that.$message({
                showClose: true,
                dangerouslyUseHTMLString: true,
                message: "<p>提交失败,请检查表单是否有误</p>",
                type: "error",
                duration: 3000
              });
              Object.keys(data).forEach(function(key) {
                that.formData.delete(key);
              });
            }
          });
        });
    },
    resetForm(formName) {
      this.$confirm("确定重置整个表单?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$refs[formName].resetFields();
      });
    },
    setFile1(e) {
      if (e.file) {
        this.formData.delete("authenticationDatas");
        this.formData.append("authenticationDatas", e.file);
      }
    },
    setFile2(e) {
      if (e.file) {
        this.formData.delete("contactInfoDatas");
        this.formData.append("contactInfoDatas", e.file);
      }
    }
  }
};
</script>


<style lang="scss" scoped>
</style>



